.card{
    background-color: seashell;
    color: #212529;
    height: 500px;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}

.card:hover{
    transform: scale(1.025);
}

.card img{
    filter: blur(2px);
}


.card img:hover{
    filter: blur(0);
}

.custom-classes{
    color: black!important;
    text-align: right;
    padding: 5px;
}